.hs-wrapper {
    padding: 25px;
    max-width: var(--min-display-w);
    margin: auto;
    overflow: visible;
    height: 500px;
    position: relative;
    display: flex;
    align-items: center;
    color: var(--col-black);


}


.hs__img-background {
    position: absolute;
    height: 700px;
    right: 0;
    z-index: -5;
    transition: all 0.5s;
    opacity: 0;


}

.hs-content {
    height: 60%;
    max-width: 60%;
    gap: 10px;
    opacity: 0;
    transition: all 0.5s;
}

.hs-el_active {
    opacity: 1;
    transition: all 1s;
}

.hs-paragraph {
    font-size: 15px;
    margin: 0;
}


.hs-heading {
    margin: 0;
    font-size: 40px;
    font-weight: 600;
    line-height: 1.2;
}

.hs-subtitle {
    font-size: 20px;
    font-weight: 400;
}

.hs-btn {
    font-size: 13px;
    padding: 10px 70px;
    border-radius: 20px;
    background-color: rgba(240, 248, 255, 0);
    border: 2px solid var(--col-gray);
    font-weight: 500;
    margin: 50px 0 0;
    cursor: pointer;
}

.hs-btn:hover {
    background-color: rgba(53, 53, 53, 0.3);
}


.hs-slider__switch-btn-block {
    position: absolute;
    bottom: 50px;
    left: 40%;
    display: flex;
    gap: 15px;

}

.hs-slider__switch-btn {
    background-color: rgba(240, 248, 255, 0);
    padding: 0;
    margin: 0;
    width: 25px;
    height: 25px;
    border: 2px solid var(--col-gray);
    border-radius: 50%;
}

.hs-slider__switch-btn_active {
    background-color: var(--col-main);
    border: none;
    position: relative;
    overflow: hidden;

    transition: all 0.5s;
}

